<template>
  <div class="content_box">
      <app-table-utils :utils="utils">
        <el-input v-model="queryData.name" slot="query-1"/>
        <el-input disabled v-model="queryData.group" slot="query-2"/>
        <el-button slot="query" class="queryBtn" >查询</el-button>
        <el-button slot="query" class="queryBtn" @click="showDialog({type:'add'})">添加用户</el-button>
      </app-table-utils>
      <div class="table-line"></div>
      <app-tables :table="table"  @pageChange="handleCurrentChange">
        <template slot="column" slot-scope="{data}">
          <template v-if="data.col.key === 'operation'">
            <el-button type="text" @click="showDialog({type:'update', data:data.row})">修改</el-button>
            <el-button type="text" @click="handleDelete(data.row)">删除</el-button>
          </template>
          <template v-else>{{data.row[data.col.key] || '-'}}</template>
        </template>
      </app-tables>
      <user-form ref="UserForm" @refresh="init()" />
  </div>
</template>
<script>
import UserForm from './form/UserForm';
export default {
  name:'UserMange',
  components: {
    UserForm
  },
  data(){
    return {
        utils: { //表格工具栏
              left: [
                  { label: '用户名', slot: 'query-1' },
                  { label: '归属分组', slot: 'query-2' },
                  { slot: 'query' },
              ],
              cols: [24]
          },
        queryData:{
          pageNum:1,
          pageSize:10,
          name:null,
          group: '下拉框'
        },
        table: {
              data: [], //表格数据
              total: 0, //总页数
              tableType: 1, //表格类型
              cols: [ //表格列配置
                  {
                      key: '序号',
                      label: '序号'
                  },
                  {
                      key: '用户',
                      label: '用户'
                  },
                  {
                      key: '账号',
                      label: '账号'
                  },
                  {
                      key: '归属分组',
                      label: '归属分组'
                  },
                   {
                      key: '备注',
                      label: '备注'
                  },
                   {
                      key: 'operation',
                      label: '操作'
                  }
              ]
          }
    }
  },
  created(){
    this.init();
  },
  methods:{
    /**
     * 初始化数据
     */
    init(){
      this.table.data = [
        {
          '序号': 1,
          '用户':'admin',
          '账号': 'admin',
          '归属分组': '成都片区',
          '备注': '该片区使用'
        }
      ]
    },


    /**
     * 分页器回调
     */
    handleCurrentChange(page) {
        this.queryData.pageNum = page;
        this.init();
    },


      /**
       * 删除
       */
      handleDelete({ id  }) {
          this.$confirm(`此操作将会删除此条数据,是否继续?`, '提示', {
              cancelButtonText: '取消',
              confirmButtonText: '确定',
              type: 'warning',
              center: true,
              customClass: 'bg-warning'
          }).then(async () => {
              // const res = await deleteOneMsg({ id  });
              // if(res.code == '1'){
              //     this.$message.success('删除成功');
              //     this.init();
              //   }else{
              //     // 失败
              //     this.$message.error('删除失败');
              //   }
          }).catch(() => {});
      },


       /**
	     * 弹出层控制
	     */
        showDialog ({ type, id, data }) {
            if (type === 'add') {
                this.$refs.UserForm.init({ type, title: '新增用户' });
            } else if (type === 'update') {
                this.$refs.UserForm.init({ type, id, data, title: '修改用户' });
            }
        },

  }
}
</script>





