<template>
    <dialog-container :dialog-conf="dialogConf">
        <el-row class="app-form plat-form" :gutter="$store.state.config.dialogFormGutterWidth">
            <el-form ref="rhForm" :model="sendData" :label-position="$store.state.config.labelPosition" :rules="rules" label-width="125px" @submit.native.prevent>
                <el-col :span="16" :offset="3">
                    <el-form-item label="用户名" prop="name">
                        <el-input v-model="sendData.name" />
                    </el-form-item>
                    <el-form-item label="账号" prop="account">
                        <el-input v-model="sendData.account" />
                    </el-form-item>
                    <el-form-item label="归属分组" prop="group">
                        <el-input v-model="sendData.group" disabled/>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input v-model="sendData.password" />
                    </el-form-item>
                    <el-form-item label="确认密码" prop="pwd">
                        <el-input v-model="sendData.pwd" />
                    </el-form-item>
                    <el-form-item label="备注" prop="remark">
                        <el-input v-model="sendData.remark" type="textarea" :rows="6" />
                    </el-form-item>
                </el-col>
                <el-col :span="24">
                    <div class="app-submit-btns">
                        <el-form-item class="app-text-center">
                            <el-button @click="submitForm('rhForm')">
                                保存
                            </el-button>
                            <el-button class="resetForm" @click="resetForm('rhForm')">
                                返回
                            </el-button>
                        </el-form-item>
                    </div>
                </el-col>
            </el-form>
        </el-row>
    </dialog-container>
</template>
<script type="text/ecmascript-6">
import util from '@/common/method/util';
// import { addRoles, modifyRoles } from '@/http';

export default {
    name: 'UserForm',
    data () {
        return {
            dialogConf: {
                width: '700px',
                isShow: false,
                center: true,
                title: null
            },
            type: null,
            sendData: {},
            initData: {
              name:null,//用户名
              account:null,//账号
              group:'下拉选择',//归属分组
              password:null,//密码
              pwd:null,//再次输入密码
              remark:null,//备注
            },

            rules: {
                name: [
                  { required: true, message: '请输入用户名', trigger: 'blur' },
                ],
                account: [
                  { required: true, message: '请输入账号', trigger: 'blur' },
                  { min: 5, max: 20, message: '长度在 5 到 20 个字符', trigger: 'blur' }
                ],
                group: [
                  { required: true, message: '请选择归属分组', trigger: 'change' },
                ],
                password: [
                  { required: true, message: '请输入密码', trigger: 'blur' },
                ],
                pwd: [
                  { required: true, message: '请再次输入密码', trigger: 'blur' },
                ],
            },
            id: null
        };
    },
    methods: {

        /**
		 * 初始化
		 */
        async init ({ type, title, id, data }) {
            this.dialogConf.isShow = true;
            this.id = id;
            this.type = type;
            if (title) this.dialogConf.title = title;
            if (type === 'add') {
                this.sendData = util.deepCloneObject(this.initData);
            } else if (type === 'update') {
                this.sendData = { ...util.deepCloneObject(this.initData), ...data };
            }
        },

        /**
		 * 提交表单
		 */
        submitForm (formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    // let code = null;
                    // if (this.type === 'update') { //修改
                    //     const ress = await modifyRoles(this.sendData);
                    //     code = ress.code;
                    // } else { //新增
                    //     this.sendData.pid = this.id;
                    //     const res = await addRoles(this.sendData);
                    //     code = res.code;
                    // }
                    // if (code == 200) {
                    //     this.dialogConf.isShow = false;
                    //     this.$message.success(this.$t('msg.operation_success'));
                    //     this.resetForm(formName);
                    //     this.$emit('refresh');
                    // };
                } else {
                    this.$message.error('请填写完表单');
                }
            });
        },

    /**
		 * 重置表单
		 * @param formName
		 */
      resetForm (formName) {
        this.$refs[formName].resetFields();
        this.dialogConf.isShow = false;
      }
    }
};
</script>
<style lang="less" scoped>
.plat-form {
    margin: 15px 0 0 0;
    .avatar-uploader-icon:hover {
        border-color: #409eff;
    }
    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
    .app-submit-btns {
        margin-top: 20px;
        text-align: center;
        /deep/ .el-form-item__content{
          margin-left: 20px !important;
        }
    }
}
</style>



